<template>
  <div class="subPage">
    <div class="pageTitle">医疗废物年度汇总</div>
    <div>
      <el-date-picker v-model="dates" type="year" laceholder="选择年" value-format="yyyy"></el-date-picker>
      <span style="margin-left: 20px">
        <el-radio-group v-model="radio" @change="search">
          <el-radio :label="1">重量</el-radio>
          <el-radio :label="2">数量</el-radio>
        </el-radio-group>
      </span>
      <el-button class="btn_search" type="success" round @click="search">搜索</el-button>
    </div>
    <div class="contents">
      <el-table :data="tableData" @change="search" show-summary style="width: 100%">
        <el-table-column prop="monthday" label="月份" width="120"></el-table-column>
        <el-table-column prop="type1" label="感染性" width="120"></el-table-column>
        <el-table-column prop="type2" label="损伤性" width="120"></el-table-column>
        <el-table-column prop="type3" label="病理性" width="120"></el-table-column>
        <el-table-column prop="type4" label="化学性" width="120"></el-table-column>
        <el-table-column prop="type5" label="药物性" width="120"></el-table-column>
        <el-table-column v-if="radio==1" prop="zzl" label="重量（kg）" width="120"></el-table-column>
        <el-table-column v-if="radio==2 " prop="zsl" label="数量" width="120"></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import {_Widget} from "../../common";
import {Downloader} from "../../common";
import {commonConfig} from "../../common";

export default {
  data() {
    return {
      dates: '',
      tableData:[],
      radio:1
    };
  },
  methods: {
    search(){
      var thisObject = this;
      var data = {
        type:'yffltj',
        startdate:thisObject.dates
      };
      if(thisObject.radio == 2){
        data = {
          type:'yffltj_count',
          startdate:thisObject.dates
        };
      }
      var url = commonConfig.baseURL + "of_yf_cx";
      Downloader.loadXMLAsync(url + "?as_json=" + JSON.stringify(data), function(e) {
        var xmldoc = e.response;
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          for(var i = 0; i < res.data.length; i++){
            switch (res.data[i].type){
              case 1:
                res.data[i].typeName = "感染性";
                break;
              case 2:
                res.data[i].typeName = "损伤性";
                break;
              case 3:
                res.data[i].typeName = "病理性";
                break;
              case 4:
                res.data[i].typeName = "化学性";
                break;
              case 5:
                res.data[i].typeName = "药物性";
                break;
              default:
            }
          }
          thisObject.tableData = res.data;
        }else{
          thisObject.$message({
            type: 'error',
            message: '未查询到相关数据!'
          });
        }
      },function () {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    }
  },
  created() {

  }

}
</script>
<style lang="scss" scoped>
@import '~@/assets/scss/subPage';
</style>
